table {
  border-collapse: collapse;
  margin-bottom: 20px;
}

.tdc {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

.thc {
  background-color: #c79595;
  font-weight: normal;
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

.spsf {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  margin-right: 20px;
}

.inputcss {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  border: 2px solid red;
  border-radius: 4px;
  box-sizing: border-box;
}


.buttoncss {
  width: 100%;
  padding: 12px 20px;
  margin: 4px 0;
  border: 2px solid red;
  border-radius: 4px;
  background-color: #c79595;
  box-sizing: border-box;
  color: white;
}

.buttoncss:hover {
  background-color: red;
  color: white;
}
.btrt{
  position: absolute;
  bottom: 20px;
  right: 1px;
}
.subBtn{
  display: flex;
  justify-content: space-around;
